"use client";

import { useMemo } from "react";
import { createPageDataFromTemplate, templates } from "../../lib/templates";
import type { PageData } from "../../lib/schemas";
import { PuckProvider } from "../../components/editor/puck-provider";
import { useBuilderStore } from "../../lib/store";
import { saveProject } from "../../lib/storage";

type SearchParams = { template?: keyof typeof templates };

export default function BuilderPage({ searchParams }: { searchParams?: SearchParams }) {
  const templateId = (searchParams?.template ?? "luxuryVillas") as keyof typeof templates;

  const initialData: PageData = useMemo(() => createPageDataFromTemplate(templateId), [templateId]);
  const { pageData, setPageData, updateSections, saveCurrent } = useBuilderStore();

  // Initialize store on first render
  if (!pageData) {
    setPageData(initialData);
  }

  return (
    <div className="min-h-screen bg-background text-foreground">
      <div className="flex items-center justify-between border-b px-4 py-2">
        <div className="text-sm text-muted-foreground">Template: {templateId}</div>
        <div className="flex items-center gap-2">
          <button
            className="rounded bg-zinc-900 px-3 py-1 text-sm text-white dark:bg-white dark:text-black"
            onClick={() => {
              if (!pageData) return;
              saveProject(pageData.id, pageData, pageData.name);
              alert("Saved to LocalStorage");
            }}
          >
            Save
          </button>
          {pageData && (
            <a
              href={`/preview?id=${encodeURIComponent(pageData.id)}`}
              className="rounded border px-3 py-1 text-sm"
              title="Open Preview"
            >
              Preview
            </a>
          )}
        </div>
      </div>
      <PuckProvider
        data={pageData ?? initialData}
        onPublish={(puckData) => {
          const sections = (puckData?.content ?? []) as PageData["sections"];
          updateSections(sections);
          const next = { ...(pageData ?? initialData), sections } as PageData;
          saveProject(next.id, next, next.name);
          alert("Published & Saved. Open Preview to see it.");
        }}
      />
    </div>
  );
}