"use client";

import { Puck, type Config } from "@measured/puck";
import "@measured/puck/puck.css";
import type { PageData, Section } from "../../lib/schemas";
import { HeroSection } from "../site/hero";
import { FAQSection } from "../site/faq";

type PuckData = { components: Record<string, unknown>; content: Section[] };

// Simple site renderers for demo. In a full build, import components/site/*
function HeroRender(props: any) {
  return <HeroSection {...props} />;
}

function FAQRender(props: any) {
  return <FAQSection items={props.items} />;
}

const config: Config<any> = {
  components: {
    Hero: {
      fields: {
        heading: { type: "text" },
        subheading: { type: "text" },
        ctaLabel: { type: "text" },
        ctaHref: { type: "text" },
        bgType: { type: "select", options: ["image", "video"] },
        bgSrc: { type: "text" },
        overlayOpacity: { type: "number" },
      },
      render: HeroRender,
    },
    FAQ: {
      fields: {
        items: {
          type: "array",
          arrayFields: {
            q: { type: "text" },
            a: { type: "textarea" },
          },
        },
      },
      render: FAQRender,
    },
  },
};

export function PuckProvider({ data, onPublish }: { data: PageData; onPublish?: (data: any) => void }) {
  const puckData: PuckData = {
    components: {},
    content: data.sections as Section[],
  };

  return (
    <div className="min-h-screen">
      <Puck config={config} data={puckData} onPublish={onPublish} />
    </div>
  );
}